body {
    font-family: 'Helvetica', 'sans-serif';
    font-size: 16px;
    background-color: #ffffff;
    color: #000000;
    margin: 0;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

header {
    margin: 10px 50px;
}

main {
    height: 100%;
    /* margin: 10px 50px; */
}

.viz-container {
    height: 100%;
}

.graph {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

h1 {
    color: darkmagenta;
}

.chart {
    position: relative;
    padding: 5px;
}

#main-container {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    height: 100%;
}

#page-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 10px;
}

#top-bar {
    display: flex;
    flex-direction: row;
    height: 75px;
    background-color: red;
}

#progress-bar {
    display: flex;
    flex-direction: row;
    height: 50px;
    background-color: red;
}

#icon {
    text-align: center;
    height: auto;
    padding: 10px;
}

#onboarding {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 100;
    background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url('img/f1-old-new.jpg');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    filter: brightness(85%);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: #ffffff;
}

#onboarding #projectName {
    font-size: 100px;
}

.constructor {
    display: flex;
    flex-direction: row;
}

.visible {
    visibility: visible;

    transition-timing-function: ease-out;

    /* A litttttle slower on the way in */
    transition: 2.5s;

    /* Move into place */
    transform: translateY(0);
}

.hidden {
    visibility: hidden;
    /* This timing applies on the way OUT */
    transition-timing-function: ease-in;
    /* Quick on the way out */
    transition: 2s;
    /* Hide thing by pushing it outside by default */
    transform: translateY(-130%);
}

.onboarding-flex {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

#onboarding-popup {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 5px;
    width: 200px;
    border: 1px solid #000000;
    border-radius: 5px;
    background-color: #ffffff;
    text-align: center;
}

.w-100 {
    width: 100%;
}

.h-100 {
    height: 100%;
}

.main-wrapper {
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 10px;
    padding-right: 10px;
}

.middle-panel {
    border-radius: 10px;
}

.constructor-option {
    margin-left: 5px;
}

.accent-background {
    background: rgb(216, 216, 216);
    box-shadow: 5px 5px 10px black;
    border-radius: 2px
}

.svg-background {
    background: #eff7ff;
    border-radius: 2px;
    box-shadow: 2px 0px 1px #b5b5b5;
}

.regulation-invisible {
    display: none;
}

.regulation-visible {
    display: block;
    min-width: 0;
    word-wrap: break-word;
    background-color: #fff;
    background-clip: border-box;
    border: 1px solid #00000020;
    border-radius: 0.25rem;
    flex-direction: column;
    position: relative;
}

.legend {
    margin-block: 20px !important;
}

.progress-svg image {
    transition: x 0.5s ease-in-out;
}

label.check {
    cursor: pointer
}

label.check input {
    position: absolute;
    top: 0;
    left: 0;
    visibility: hidden;
    pointer-events: none
}

label.check span {
    padding: 7px 14px;
    border: 2px solid #8f37aa;
    display: inline-block;
    color: #8f37aa;
    border-radius: 3px;
    text-transform: uppercase
}

label.check input:checked+span {
    border-color: #8f37aa;
    background-color: #8f37aa;
    color: #fff
}

.noselect {
    -webkit-touch-callout: none;
    /* iOS Safari */
    -webkit-user-select: none;
    /* Safari */
    -khtml-user-select: none;
    /* Konqueror HTML */
    -moz-user-select: none;
    /* Old versions of Firefox */
    -ms-user-select: none;
    /* Internet Explorer/Edge */
    user-select: none;
    /* Non-prefixed version, currently
                                    supported by Chrome, Edge, Opera and Firefox */
}

.d3-tip {
    line-height: 1;
    padding: 12px;
    background: rgba(54, 32, 35, 0.8);
    color: #fff;
    border-radius: 2px;
    font-size: 10px;
    pointer-events: none;
}