body {
  color: #000;
  height: 100vh;
  background-color: #fff;
  flex-direction: column;
  margin: 0;
  font-family: Helvetica, "sans-serif";
  font-size: 16px;
  display: flex;
}

header {
  margin: 10px 50px;
}

main, .viz-container {
  height: 100%;
}

.graph {
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

h1 {
  color: #8b008b;
}

.chart {
  padding: 5px;
  position: relative;
}

#main-container {
  height: 100%;
  flex-flow: column wrap;
  display: flex;
}

#page-container {
  flex-flow: wrap;
  padding: 10px;
  display: flex;
}

#top-bar {
  height: 75px;
  background-color: red;
  flex-direction: row;
  display: flex;
}

#progress-bar {
  height: 50px;
  background-color: red;
  flex-direction: row;
  display: flex;
}

#icon {
  text-align: center;
  height: auto;
  padding: 10px;
}

#onboarding {
  width: 100%;
  height: 100%;
  z-index: 100;
  filter: brightness(85%);
  color: #fff;
  background-color: #0000;
  background-image: linear-gradient(#000000b3, #000000b3), url("f1-old-new.e1dcfbdb.jpg");
  background-position: 0 0, 0 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

#onboarding #projectName {
  font-size: 100px;
}

.constructor {
  flex-direction: row;
  display: flex;
}

.visible {
  visibility: visible;
  transition: all 2.5s;
  transform: translateY(0);
}

.hidden {
  visibility: hidden;
  transition: all 2s;
  transform: translateY(-130%);
}

.onboarding-flex {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

#onboarding-popup {
  width: 200px;
  text-align: center;
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  padding: 5px;
  display: flex;
  position: absolute;
}

.w-100 {
  width: 100%;
}

.h-100 {
  height: 100%;
}

.main-wrapper {
  padding: 20px 10px;
}

.middle-panel {
  border-radius: 10px;
}

.constructor-option {
  margin-left: 5px;
}

.accent-background {
  background: #d8d8d8;
  border-radius: 2px;
  box-shadow: 5px 5px 10px #000;
}

.svg-background {
  background: #eff7ff;
  border-radius: 2px;
  box-shadow: 2px 0 1px #b5b5b5;
}

.regulation-invisible {
  display: none;
}

.regulation-visible {
  min-width: 0;
  word-wrap: break-word;
  background-color: #fff;
  background-clip: border-box;
  border: 1px solid #00000020;
  border-radius: .25rem;
  flex-direction: column;
  display: block;
  position: relative;
}

.legend {
  margin-block: 20px !important;
}

.progress-svg image {
  transition: x .5s ease-in-out;
}

label.check {
  cursor: pointer;
}

label.check input {
  visibility: hidden;
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
}

label.check span {
  color: #8f37aa;
  text-transform: uppercase;
  border: 2px solid #8f37aa;
  border-radius: 3px;
  padding: 7px 14px;
  display: inline-block;
}

label.check input:checked + span {
  color: #fff;
  background-color: #8f37aa;
  border-color: #8f37aa;
}

.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
  -khtml-user-select: none;
}

.d3-tip {
  color: #fff;
  pointer-events: none;
  background: #362023cc;
  border-radius: 2px;
  padding: 12px;
  font-size: 10px;
  line-height: 1;
}

